import { Problem } from '@/generated/problem/problem.model';
import { Args, Info, Int, ResolveField, Resolver } from '@nestjs/graphql';
import { CreateProblemInput } from './dto/create-problem.input';
import { UpdateProblemInput } from './dto/update-problem.input';
import { ProblemService } from './problem.service';
import { ProblemMutations } from './problem.type';
import { ProblemCreateInput } from '../../generated/problem/problem-create.input';
import { info } from 'console';
import { PrismaSelect } from '@paljs/plugins';

@Resolver(() => ProblemMutations)
export class ProblemMutationsResolver {
  constructor(private readonly problemService: ProblemService) {}

  @ResolveField(() => Problem)
  createProblem(@Args('data') data: ProblemCreateInput, @Info() info) {
    const select = new PrismaSelect(info).value;
    return this.problemService.create(data, select);
  }
  @ResolveField(() => Problem)
  updateProblem(@Args('updateProblemInput') updateProblemInput: UpdateProblemInput) {
    return this.problemService.update(updateProblemInput.id, updateProblemInput);
  }

  @ResolveField(() => Problem)
  removeProblem(@Args('id', { type: () => Int }) id: number) {
    return this.problemService.remove(id);
  }
}
