import { Request } from 'express';
import { ForbiddenException, HttpException, Injectable } from '@nestjs/common';
import { ConfigService } from '@nestjs/config';
import { Strategy, ExtractJwt } from 'passport-jwt';
import { PassportStrategy } from '@nestjs/passport';

import { AuthService } from '../auth.service';
import { JwtDto } from '../jwt.interface';
import { decrypt } from '../../../utils/crypto.util';
import { storage } from 'nestjs-pino/storage';
import { PinoLogger } from 'nestjs-pino';

@Injectable()
export class JwtStrategy extends PassportStrategy(Strategy) {
  constructor(
    private readonly authService: AuthService,
    readonly configService: ConfigService,
    private readonly logger: PinoLogger,
  ) {
    super({
      jwtFromRequest: ExtractJwt.fromExtractors([
        (request: Request) => {
          const signature = request?.cookies?.['a_sign'];
          const header = request?.cookies?.['a_header'];
          const payload = request?.headers?.['x-access-payload'];
          // if (!signature || !header || !payload) {
          //   throw new ForbiddenException({ message: 'No token found', level: 'error' });
          // }
          const temp =
            'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..0tas52LjLidtMbksQ9jabl_cHaoGAUE-HyUQE-s162Q';
          return temp;
          return `${header}.${payload}.${signature}`;
        },
      ]),
      ignoreExpiration: false,
      secretOrKey: configService.get('JWT_ACCESS_SECRET'),
    });
  }

  async validate(payload: JwtDto) {
    const privateData = JSON.parse(decrypt(payload.private));

    // this.logger.assign({ user: payload.userId });
    return { ...payload, ...privateData };
  }
}
