import { Field } from '@nestjs/graphql';
import { InputType } from '@nestjs/graphql';
import { HideField } from '@nestjs/graphql';
import { Float } from '@nestjs/graphql';
import { Int } from '@nestjs/graphql';
import { GraphQLJSON } from 'graphql-type-json';
import { ProblemCreateacceptLanguageInput } from '../prisma/problem-createaccept-language.input';
import { ProblemCreatecompanyTagsInput } from '../prisma/problem-createcompany-tags.input';
import { ProblemCreatetopicTagsInput } from '../prisma/problem-createtopic-tags.input';
import { ProblemCreatecontributorsInput } from '../prisma/problem-createcontributors.input';

@InputType()
export class ProblemCreateInput {

    @Field(() => String, {nullable:false})
    name!: string;

    @HideField()
    slug?: string;

    @Field(() => Float, {nullable:true})
    acceptRate?: number;

    @HideField()
    commentCount?: number;

    @Field(() => Int, {nullable:true})
    contestId?: number;

    @Field(() => String, {nullable:true})
    status?: string;

    @Field(() => String, {nullable:true})
    category?: string;

    @Field(() => String, {nullable:true})
    description?: string;

    @Field(() => GraphQLJSON, {nullable:true})
    examples?: any;

    @Field(() => String, {nullable:true})
    inputType?: string;

    @Field(() => String, {nullable:true})
    outputType?: string;

    @Field(() => Int, {nullable:true})
    highlightSolutionCount?: number;

    @Field(() => GraphQLJSON, {nullable:true})
    hint?: any;

    @Field(() => Boolean, {nullable:true})
    isFavorited?: boolean;

    @Field(() => Int, {nullable:true})
    officalSolutionCount?: number;

    @Field(() => GraphQLJSON, {nullable:true})
    codeSnippets?: any;

    @Field(() => GraphQLJSON, {nullable:true})
    exampleTestcases?: any;

    @Field(() => String, {nullable:true})
    difficulty?: string;

    @Field(() => Int, {nullable:true})
    likes?: number;

    @Field(() => Int, {nullable:true})
    dislikes?: number;

    @Field(() => GraphQLJSON, {nullable:true})
    solutions?: any;

    @HideField()
    domainId?: number;

    @HideField()
    createdById?: number;

    @HideField()
    createdByUsername?: string;

    @Field(() => String, {nullable:true})
    createdByName?: string;

    @Field(() => Date, {nullable:true})
    createdAt?: Date | string;

    @Field(() => Date, {nullable:true})
    updatedAt?: Date | string;

    @Field(() => ProblemCreateacceptLanguageInput, {nullable:true})
    acceptLanguage?: ProblemCreateacceptLanguageInput;

    @Field(() => ProblemCreatecompanyTagsInput, {nullable:true})
    companyTags?: ProblemCreatecompanyTagsInput;

    @Field(() => ProblemCreatetopicTagsInput, {nullable:true})
    topicTags?: ProblemCreatetopicTagsInput;

    @Field(() => ProblemCreatecontributorsInput, {nullable:true})
    contributors?: ProblemCreatecontributorsInput;
}
