import { Args, Int, Resolver, ResolveField, Info } from '@nestjs/graphql';

import { User } from '@/generated/user/user.model';
import { UserCreateInput } from '@/generated/user/user-create.input';
import { UserUpdateInput } from '@/generated/user/user-update.input';
import { UserWhereUniqueInput } from '@/generated/user/user-where-unique.input';

import { UserMutations } from './user.type';
import { UserService } from './user.service';
import { GraphQLResolveInfo } from 'graphql';
import { PrismaSelect } from '@paljs/plugins';

@Resolver(() => UserMutations)
export class UserMutationResolver {
  constructor(private readonly userService: UserService) {}

  @ResolveField(() => User)
  createUser(@Args('data') data: UserCreateInput, @Info() info: GraphQLResolveInfo) {
    const defaultFields = { User: { id: true } };
    const { select } = new PrismaSelect(info, { defaultFields }).value;
    return this.userService.create(data, select);
  }

  @ResolveField(() => User)
  updateUser(
    @Args('data') data: UserUpdateInput,
    @Args('where') where: UserWhereUniqueInput,
  ) {
    return this.userService.update(data, where);
  }

  @ResolveField(() => User)
  removeUser(@Args('id', { type: () => Int }) id: number) {
    return this.userService.remove(id);
  }
}
