import { nanoid } from 'nanoid';
import { Injectable, NotImplementedException } from '@nestjs/common';
import { PrismaService } from 'nestjs-prisma';
import { UserInputError } from 'apollo-server-express';

import { PasswordService } from './password.service';
import { UserCreateInput } from '@/generated/user/user-create.input';
import { UserUpdateInput } from '@/generated/user/user-update.input';
import { FindManyUserArgs } from '@/generated/user/find-many-user.args';
import { UserWhereUniqueInput } from '@/generated/user/user-where-unique.input';
import { Prisma } from '@prisma/client';
import { User } from '@/generated/user/user.model';

@Injectable()
export class UserService {
  constructor(private prisma: PrismaService, private passwordService: PasswordService) {}
  async create(data: UserCreateInput, select?: object) {
    const isExisted = await this.findUserByEmail(data.email, { id: true });
    if (isExisted) {
      throw new UserInputError('User with that email already exists');
    }

    try {
      const hashedPassword = await this.passwordService.hashPassword(
        data.password || nanoid(8),
      );
      const user = await this.prisma.user.create({
        data: { ...data, password: hashedPassword },
        select,
      });

      return user;
    } catch (error) {
      throw new UserInputError('Fail when create user');
    }
  }

  findUserByEmail(email: string, select?: object) {
    return this.prisma.user.findUnique({ where: { email }, select });
  }

  findMany(args: FindManyUserArgs, select: object) {
    return this.prisma.user.findMany({ ...args, ...select });
  }

  findUnique(where: UserWhereUniqueInput, select?: Prisma.UserSelect) {
    return this.prisma.user.findUnique({ where, select });
  }

  update(data: UserUpdateInput, where: UserWhereUniqueInput): Promise<User> {
    return this.prisma.user.update({ data, where });
  }

  remove(id: number) {
    return `This action removes a #${id} user`;
  }
}
