import { MiddlewareConsumer, Module, NestModule } from '@nestjs/common';
import { PrismaModule } from 'nestjs-prisma';
import { ConfigModule } from '@nestjs/config';
import { GraphQLModule } from '@nestjs/graphql';
import { ApolloDriver, ApolloDriverConfig } from '@nestjs/apollo';
import { LoggerInjector, OpenTelemetryModule } from '@metinseylan/nestjs-opentelemetry';

import config from '@/configs/config';
import { GqlConfig } from '@/configs/graphql.config';
import { LoggerModule } from '@/logger/logger.module';
import trackIpMiddleware from '@/middleware/trackip.middleware';
import { loggingMiddleware } from '@/middleware/logging.middleware';

import { AppService } from './app.service';
import { AppController } from './app.controller';
import { RoleModule } from '../modules/role/role.module';
import { UserModule } from '../modules/user/user.module';
import { PostModule } from '../modules/post/post.module';
import { AuthModule } from '../modules/auth/auth.module';
import { GroupModule } from '../modules/group/group.module';
import { RoleController } from '../modules/role/role.controller';
import { ProblemModule } from '../modules/problem/problem.module';
import { RoleGroupModule } from '../modules/casbin/role/role.module';
import { PolicyModule } from '../modules/casbin/policy/policy.module';

@Module({
  imports: [
    ConfigModule.forRoot({ isGlobal: true, load: [config] }),
    PrismaModule.forRootAsync({
      isGlobal: true,
      useFactory: () => ({
        prismaOptions: { log: ['info', 'query'] },
        middlewares: [loggingMiddleware()],
      }),
    }),
    GraphQLModule.forRootAsync<ApolloDriverConfig>({
      driver: ApolloDriver,
      useClass: GqlConfig,
    }),
    OpenTelemetryModule.forRoot({
      traceAutoInjectors: [LoggerInjector],
      applicationName: 'graphql-api-gateway',
    }),
    RoleModule,
    LoggerModule,
    UserModule,
    PostModule,
    ProblemModule,
    AuthModule,
    GroupModule,
    PolicyModule,
    RoleGroupModule,
  ],
  controllers: [AppController, RoleController],
  providers: [AppService],
})
export class AppModule implements NestModule {
  configure(consumer: MiddlewareConsumer) {
    consumer.apply(trackIpMiddleware).forRoutes('*');
  }
}
