import { UseGuards } from '@nestjs/common';
import { GraphQLResolveInfo } from 'graphql';
import { PrismaSelect } from '@paljs/plugins';
import { Args, Resolver, ResolveField, Info } from '@nestjs/graphql';

import { Role } from '@/generated/role/role.model';
import { GqlPolicyGuard } from '@/modules/casbin/policy.guard';
import { RoleMutations } from '@/modules/casbin/role/role.type';
import { Permissions } from '@/modules/casbin/permisions.decorator';
import { GqlAuthGuard } from '@/modules/auth/guards/gql-auth.guard';
import { RoleUpdateInput } from '@/generated/role/role-update.input';
import { RoleWhereUniqueInput } from '@/generated/role/role-where-unique.input';

import { RoleService } from './role.service';
import { RoleCreateInput } from './dto/role-create.input';

@Resolver(() => RoleMutations)
export class RoleMutationsResolver {
  constructor(private readonly roleService: RoleService) {}

  @ResolveField(() => Role)
  @UseGuards(GqlAuthGuard, GqlPolicyGuard)
  @Permissions({
    action: 'create',
    resource: 'role',
  })
  createRole(@Args('data') data: RoleCreateInput, @Info() info: GraphQLResolveInfo) {
    const select = new PrismaSelect(info).value;
    return this.roleService.createRole(data, select);
  }

  @ResolveField(() => Role)
  @UseGuards(GqlAuthGuard, GqlPolicyGuard)
  @Permissions({
    action: 'update',
    resource: 'role',
  })
  updateRole(
    @Args('data') data: RoleUpdateInput,
    @Args('where') where: RoleWhereUniqueInput,
    @Info() info: GraphQLResolveInfo,
  ) {
    const { select } = new PrismaSelect(info).value;
    return this.roleService.update(data, where, select);
  }

  @ResolveField(() => Role)
  @UseGuards(GqlAuthGuard, GqlPolicyGuard)
  @Permissions({
    action: 'delete',
    resource: 'role',
  })
  removeRole(
    @Args('where') where: RoleWhereUniqueInput,
    @Info() info: GraphQLResolveInfo,
  ) {
    const { select } = new PrismaSelect(info).value;
    return this.roleService.remove(where, select);
  }
}
