import {
  ConflictException,
  Injectable,
  InternalServerErrorException,
} from '@nestjs/common';
import { PrismaService } from 'nestjs-prisma';
import { UpdateProblemInput } from './dto/update-problem.input';
import { ProblemCreateInput } from '../../generated/problem/problem-create.input';
import slugify from 'slugify';
import { Prisma } from '@prisma/client';

@Injectable()
export class ProblemService {
  constructor(private prisma: PrismaService) {}
  async create(data: ProblemCreateInput, select: object) {
    data.slug = slugify(data.name);
    return this.prisma.problem.create({ data, ...select }).catch((error) => {
      if (error instanceof Prisma.PrismaClientKnownRequestError) {
        if (error.code === 'P2002') {
          throw new ConflictException({ message: 'Problem is exists' });
        }
      }
      throw error;
    });
  }

  findMany() {
    return this.prisma.problem.findMany();
  }

  findUnique(id: number) {
    return `This action returns a #${id} problem`;
  }

  update(id: number, updateProblemInput: UpdateProblemInput) {
    return `This action updates a #${id} problem`;
  }

  remove(id: number) {
    return `This action removes a #${id} problem`;
  }
}
