// import { Logger } from '@nestjs/common';
import { UseGuards } from '@nestjs/common';
import { GraphQLResolveInfo } from 'graphql';
import { PrismaSelect } from '@paljs/plugins';
import { Resolver, Query, Mutation, Args, Info } from '@nestjs/graphql';

import { Role } from '@/generated/role/role.model';
import { GqlPolicyGuard } from '@/modules/casbin/policy.guard';
import { RoleMutations } from '@/modules/casbin/role/role.type';
import { Permissions } from '@/modules/casbin/permisions.decorator';
import { GqlAuthGuard } from '@/modules/auth/guards/gql-auth.guard';
import { FindManyRoleArgs } from '@/generated/role/find-many-role.args';
import { RoleWhereUniqueInput } from '@/generated/role/role-where-unique.input';

import { RoleService } from './role.service';
import { Logger } from '@/logger/logger.service';

@Resolver(() => Role)
export class RoleResolver {
  constructor(
    private readonly roleService: RoleService,
    private readonly logger: Logger,
  ) {
    // this.logger.setContext(RoleResolver.name);
  }

  @Mutation(() => RoleMutations, { name: 'role', nullable: true })
  roleMutations() {
    return {};
  }

  @Query(() => [Role], { name: 'roles' })
  // @UseGuards(GqlAuthGuard, GqlPolicyGuard)
  // @Permissions({
  //   action: 'read',
  //   resource: 'role',
  //   noUniqueInput: true,
  // })
  findMany(@Args() args: FindManyRoleArgs, @Info() info: GraphQLResolveInfo) {
    this.logger.info('hello');
    const { select } = new PrismaSelect(info).value;
    return this.roleService.findMany(args, select);
  }

  @Query(() => Role, { name: 'role' })
  @UseGuards(GqlAuthGuard, GqlPolicyGuard)
  @Permissions({
    action: 'read',
    resource: 'role',
  })
  findUnique(
    @Args('where') where: RoleWhereUniqueInput,
    @Info() info: GraphQLResolveInfo,
  ) {
    const { select } = new PrismaSelect(info).value;
    return this.roleService.findUnique(where, select);
  }
}
