import { UseGuards } from '@nestjs/common';
import { GraphQLResolveInfo } from 'graphql';
import { PrismaSelect } from '@paljs/plugins';
import { Resolver, Query, Mutation, Args, Int, Info } from '@nestjs/graphql';

import { User } from '@/generated/user/user.model';

import { UserMutations } from './user.type';
import { UserService } from './user.service';
import { GqlAuthGuard } from '../auth/guards/gql-auth.guard';
import { FindManyUserArgs } from '../../generated/user/find-many-user.args';

@Resolver(() => User)
export class UserResolver {
  constructor(private readonly userService: UserService) {}

  @Mutation(() => UserMutations, { name: 'user', nullable: true })
  userMutation() {
    return {};
  }

  @Query(() => [User], { name: 'users' })
  @UseGuards(GqlAuthGuard)
  users(@Args() args: FindManyUserArgs, @Info() info: GraphQLResolveInfo) {
    const select = new PrismaSelect(info).value;
    return this.userService.findMany(args, select);
  }

  // @Query(() => User, { name: 'user' })
  // findUnique(@Args('id', { type: () => Int }) id: number) {
  //   return this.userService.findUnique(id);
  // }
}
