import slugify from 'slugify';
import { Prisma } from '@prisma/client';
import { PrismaService } from 'nestjs-prisma';
import {
  ConflictException,
  Injectable,
  NotFoundException,
  NotImplementedException,
} from '@nestjs/common';

import { RoleCreateInput } from './dto/role-create.input';
import { RoleUpdateInput } from '@/generated/role/role-update.input';
import { FindManyRoleArgs } from '@/generated/role/find-many-role.args';
import { RoleWhereUniqueInput } from '@/generated/role/role-where-unique.input';

import { RoleGroupService } from '../casbin/role/role.service';
import { PolicyService } from '../casbin/policy/policy.service';
import { Logger } from '@/logger/logger.service';
import { context, trace } from '@opentelemetry/api';

@Injectable()
export class RoleService {
  constructor(
    private readonly casbinPolicyService: PolicyService,
    private readonly roleGroupService: RoleGroupService,
    private readonly prisma: PrismaService,
  ) {}

  async createRole(data: RoleCreateInput, select?: object) {
    const { createdById, createdByName, domainId } = data;
    const role = slugify(data.name);

    const checkExist = await this.prisma.role.findFirst({
      where: { role, domainId },
      select: { id: true },
    });
    if (checkExist) {
      throw new ConflictException('Role is exists');
    }

    data.polices.forEach((element) => {
      Object.assign(element, {
        domainId,
        createdById,
        createdByName,
        subject: role,
      });
    });

    await this.casbinPolicyService.createPolicies(data.polices);

    // TODO handle when inherit
    // await this.roleGroupService.createRole(
    //   {
    //     domainId,
    //     rule: slug,
    //     role,
    //   },
    //   { id: true },
    // );

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { polices, ...dataCreate } = data;
    dataCreate.role = role;

    return this.prisma.role.create({ data: dataCreate, ...select });
  }

  findMany(args: FindManyRoleArgs, select: object) {
    return this.prisma.role.findMany({ ...args, select });
  }

  async findUnique(where: RoleWhereUniqueInput, select: Prisma.RoleSelect) {
    return this.prisma.role.findUnique({ where, select }).then((data) => {
      if (!data) {
        throw new NotFoundException();
      }
      return data;
    });
  }

  update(data: RoleUpdateInput, where: RoleWhereUniqueInput, select: object) {
    return this.prisma.role.update({ data, where, select }).catch((e) => {
      if (e instanceof Prisma.PrismaClientKnownRequestError) {
        if (e.code === 'P2025') {
          throw new NotFoundException();
        }
      }
      throw new NotImplementedException(e.meta?.cause);
    });
  }

  remove(where: RoleWhereUniqueInput, select: object) {
    return this.prisma.role.delete({ where, select }).catch((e) => {
      if (e instanceof Prisma.PrismaClientKnownRequestError) {
        if (e.code === 'P2025') {
          throw new NotFoundException();
        }
      }
      throw new NotImplementedException(e.meta?.cause);
    });
  }
}
