import { Field } from '@nestjs/graphql';
import { ObjectType } from '@nestjs/graphql';
import { ID } from '@nestjs/graphql';
import { Int } from '@nestjs/graphql';
import { Float } from '@nestjs/graphql';
import { GraphQLJSON } from 'graphql-type-json';
import { HideField } from '@nestjs/graphql';

@ObjectType()
export class Problem {

    @Field(() => ID, {nullable:false})
    id!: number;

    @Field(() => String, {nullable:false})
    name!: string;

    @Field(() => String, {nullable:true})
    slug!: string | null;

    @Field(() => [Int], {nullable:true})
    acceptLanguage!: Array<number>;

    @Field(() => Float, {nullable:true})
    acceptRate!: number | null;

    @Field(() => Int, {nullable:true})
    commentCount!: number | null;

    @Field(() => Int, {nullable:true})
    contestId!: number | null;

    @Field(() => String, {nullable:false,defaultValue:'disabled'})
    status!: string;

    @Field(() => String, {nullable:true})
    category!: string | null;

    @Field(() => String, {nullable:true})
    description!: string | null;

    @Field(() => [Int], {nullable:true})
    companyTags!: Array<number>;

    @Field(() => [Int], {nullable:true})
    topicTags!: Array<number>;

    @Field(() => [GraphQLJSON], {nullable:true})
    contributors!: Array<any>;

    @Field(() => GraphQLJSON, {nullable:true})
    examples!: any | null;

    @Field(() => String, {nullable:true})
    inputType!: string | null;

    @Field(() => String, {nullable:true})
    outputType!: string | null;

    @Field(() => Int, {nullable:true})
    highlightSolutionCount!: number | null;

    @Field(() => GraphQLJSON, {nullable:true})
    hint!: any | null;

    @Field(() => Boolean, {nullable:false,defaultValue:false})
    isFavorited!: boolean;

    @Field(() => Int, {nullable:true})
    officalSolutionCount!: number | null;

    @Field(() => GraphQLJSON, {nullable:true})
    codeSnippets!: any | null;

    @Field(() => GraphQLJSON, {nullable:true})
    exampleTestcases!: any | null;

    @Field(() => String, {nullable:false,defaultValue:'easy'})
    difficulty!: string;

    @Field(() => Int, {nullable:false,defaultValue:0})
    likes!: number;

    @Field(() => Int, {nullable:false,defaultValue:0})
    dislikes!: number;

    @Field(() => GraphQLJSON, {nullable:true})
    solutions!: any | null;

    @HideField()
    domainId!: number;

    @HideField()
    createdById!: number | null;

    @Field(() => String, {nullable:true})
    createdByUsername!: string | null;

    @Field(() => String, {nullable:true})
    createdByName!: string | null;

    @Field(() => Date, {nullable:false})
    createdAt!: Date;

    @Field(() => Date, {nullable:true})
    updatedAt!: Date | null;
}
