import {
  ExceptionFilter,
  Catch,
  ArgumentsHost,
  HttpException,
  HttpStatus,
  Logger,
  InternalServerErrorException,
} from '@nestjs/common';
import { BaseExceptionFilter } from '@nestjs/core';
import { GqlArgumentsHost, GqlExceptionFilter } from '@nestjs/graphql';
import { trace, context, Exception } from '@opentelemetry/api';
import { GraphQLResolveInfo } from 'graphql';

@Catch()
export class AllExceptionsFilter implements ExceptionFilter, GqlExceptionFilter {
  catch(exception: HttpException, host: ArgumentsHost) {
    console.log(
      ' ~ file: exceptions.ts ~ line 17 ~ AllExceptionsFilter ~ exception',
      exception,
      'end',
    );
    const ctx = host.switchToHttp();
    const status = exception.getStatus
      ? exception.getStatus()
      : HttpStatus.INTERNAL_SERVER_ERROR;

    const errorResponse = {
      statusCode: status,
      error:
        status !== HttpStatus.INTERNAL_SERVER_ERROR
          ? exception.message || null
          : 'Internal server error',
    };

    if (host.getType<'graphql'>() === 'graphql') {
      if (status === HttpStatus.INTERNAL_SERVER_ERROR) {
        return new InternalServerErrorException('Co loi tu he thong');
      }
      return exception;
    }

    const gqlHost = GqlArgumentsHost.create(host);
    const info = gqlHost.getInfo<GraphQLResolveInfo>();

    const response = ctx.getResponse();

    return response.status(status).json(errorResponse);
  }
}

