import { Resolver, Query, Mutation, Args, Int } from '@nestjs/graphql';

import { Problem } from '@/generated/problem/problem.model';

import { ProblemMutations } from './problem.type';
import { ProblemService } from './problem.service';

@Resolver(() => Problem)
export class ProblemResolver {
  constructor(private readonly problemService: ProblemService) {}

  @Mutation(() => ProblemMutations, { name: 'problem', nullable: true })
  problemMutations() {
    return {};
  }
  @Query(() => [Problem], { name: 'problems' })
  findMany() {
    return this.problemService.findMany();
  }

  @Query(() => Problem, { name: 'problem' })
  findUnique(@Args('id', { type: () => Int }) id: number) {
    return this.problemService.findUnique(id);
  }
}
