import { transformObjectToQuery } from '@/utils/database-query';

export const policyByRoleResourceQuery = ({ roleId, resource }: any, select: any) => {
  const transformSelect = transformObjectToQuery(select, 'cp');
  return `
    select
      ${transformSelect}
    from
      casbin_policy cp
    where
      cp.subject in (
        select
          cr."rule"
        from
          role r
        join casbin_role cr on
          r.role = cr.role
        where
          r."id" = '${roleId}'
      )
    and cp."object" = '${resource}'
`;
};
